body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e6ffe6;
  color: #113311;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 300px;
  margin: 0 auto;
}
.number-input {
  margin-bottom: 15px;
}

.number-input__label {
  display: block;
  width: 100%;
  margin-bottom: 3px;
}

.number-input__wrapper {
  position: relative;
}

.number-input__wrapper > input {
  padding: 5px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
}

.number-input__wrapper--icon> input {
  padding: 5px 5px 5px 20px;
}

.number-input__wrapper--icon-right> input {
  padding: 5px 20px 5px 5px;
}

.number-input__wrapper--icon > i {
  position: absolute;
  display: block;
  top: 0;
  padding: 4px;
  pointer-events: none;
  width: 20px;
  text-align: center;
	font-style: normal;
}

.number-input__wrapper--icon-right > i {
  right: 0;
}

.number-input__error {
  color: red;
  font-size: 12px;
  width: 100%;
}

.result-list {
  list-style-type: none;
  padding: 0;
  margin-top: 50px;
}

.result-list h3 {
  margin-bottom: 10px;
}

.result-list li {
  padding: 5px 0;
}

.result-list__td {
  padding: 2px 20px 2px 0;
}
button {
  padding: 10px 20px;
  margin: 20px 0;
  border: none;
  background: #ff88ff;
  font-size: 17px;
  cursor: pointer;
}

button.disabled {
  background: lightgray;
  color: gray;
}
